.all-form {

  &__input {
    padding: 12px 16px;
    width: 100%;
    height: 43px;
    border-radius: 2px;
    border: 1px solid var(--Black300, #8E8B82);
    opacity: 0.3;
    transition: 0.2s ease-in-out;

    &:focus {
      border: 1px solid var(--black-900);
      opacity: 1;
    }

    &:hover {
      border: 1px solid var(--black-300);
      opacity: 1;
    }

    &--drop {
      margin-bottom: 8px;
      width: 240px;
    }

    &--registr {
      width: 100%;

      &:not(:last-child) {
        margin-bottom: 8px;
      }
    }

    &--profile {
      width: 100%;
    }
  }

  &__textarea {
    padding: 12px 16px;
    width: 100%;
    height: 81px;
    border-radius: 2px;
    border: 1px solid var(--Black300, #8E8B82);
    opacity: 0.3;
    resize:none;
    transition: 0.2s ease-in-out;

    &:focus {
      border: 1px solid var(--black-900);
      opacity: 1;
    }

    &:hover {
      border: 1px solid var(--black-300);
      opacity: 1;
    }

    @include mobile {
      height: 119px;
    }
  }
}
