.dropdown {
  position: relative;
}

.dropdown-content {
  display: none;
  position: absolute;
  padding-top: 16px;
  top: 18px;
  left: 0;
  width: var(--content-width);
  z-index: 10;
  transition: 0.2s ease-in-out;
}

.dropdown:hover .dropdown-content {display: block;}

.menu-content {
  position: absolute;
  display: none;
  top: 71px;
  right: 0;
  padding: 24px;
  border-radius: 0px 0px 8px 8px;
  border-right: 1px solid rgba(77, 76, 125, 0.50);
  border-bottom: 1px solid rgba(77, 76, 125, 0.50);
  border-left: 1px solid rgba(77, 76, 125, 0.50);
  box-shadow: 0px 2px 16px 0px rgba(52, 52, 52, 0.10);
  background-color: var(--light-color);
  z-index: 20;

  &__form {
    display: flex;
    flex-direction: column;

    span {
      margin-bottom: 16px;
      text-align: center;
      font-size: 13px;
      font-weight: 400;
      color: var(--black-300);
    }
  }

  &__btn {
    margin-bottom: 16px;
  }
}


// services

.dropdown:hover .dropdown-second {display: block;}
.dropdown:hover .nav__link--active::before {transform: rotate(90deg);}


.dropdown-second {
  display: none;
  position: absolute;
  padding-top: 16px;
  top: 18px;
  left: 0;
  width: 245px;
  z-index: 10;
  transition: 0.2s ease-in-out;
}

.menu-second {
  position: relative;
  display: flex;
  gap: 30px;
  padding: 32px 48px;
  border-radius: 0px 0px 8px 8px;
  background-color: var(--light-color);
  box-shadow: -7px 8px 0px 0px rgba(0, 73, 153, 1);
  z-index: 20;

  &__list {
    display: flex;
    flex-direction: column;
  }

  &__title {
    margin-bottom: 16px;
    font-size: 16px;
    font-weight: 700;
  }

  &__item {

    &:not(:last-child) {
      margin-bottom: 16px;
    }

    a {
      font-size: 14px;
      font-weight: 400;
      color: #000;
      transition: color 0.2s ease-in-out;

      &:hover {
        border-bottom: 1px solid var(--dark-color);
      }
    }
  }

  &__icon {
   max-width: 90px;
  }
}

// burger-mobile

.dropdown:hover .dropdown-second {display: block;}
.dropdown:hover .nav__link--active::before {transform: rotate(90deg);}
